@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
    background-color:#ffff;
    height: 100%;
    width: 100%;
    font-family: monospace;
    margin: 0;
    padding: 0;
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height:15vh;
}

.typing {
    width: 14ch;
    -webkit-animation: typing 2s steps(22), blink .5s step-end infinite alternate;
            animation: typing 2s steps(22), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-size: 2em;
}

@-webkit-keyframes typing {
    from {
        width: 0
    }
}

@keyframes typing {
    from {
        width: 0
    }
}

@-webkit-keyframes blink {
    50% {
        border-color: transparent
    }
}

@keyframes blink {
    50% {
        border-color: transparent
    }
}
.main-days {
    height: 85vh;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    padding: 0;
    margin: 0;
}
@media (max-width: 800px) {
    .main-days {
        height:100%;
    }
}
.days {
    background-color:white;
    margin: 10px 15px 10px 15px;
    width:20%;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    display:flex;
    flex-direction:column;
    align-items:stretch;
    border:1px solid #efefef;
    box-shadow:
        0 1px 1px rgba(0,0,0,0.15),
        0 10px  0 -5px #eee,
        0 10px 1px -4px rgba(0,0,0,0.15),
        0 20px 0 -10px #eee,
        0 20px 1px -9px rgba(0,0,0,0.15);
    padding:30px;
    min-height:40%;
}

.day-name{
    font-size: 1.5em;
    display:flex;
    justify-content: center;
    width:100%;
}

.text-area {
    width:80%;
    padding:5px;
    margin:auto;
    margin-top:25px;
    text-align:center;
    border:none;
    border-bottom: solid 0.5px #cecece;
    border-right: solid 0.3px #cecece;
}

.text-area::-webkit-input-placeholder {
    font-size:0.8em;
}

.text-area:-ms-input-placeholder {
    font-size:0.8em;
}

.text-area::-ms-input-placeholder {
    font-size:0.8em;
}

.text-area::placeholder {
    font-size:0.8em;
}

.text-area:focus {
    outline:none;
}

@media (max-width: 800px) {
    .days {
        width:50%;
    }
}
.day-content {
    display:flex;
    justify-content:flex-start;
    align-items:center;
    padding:10px 20px 0;
    border-bottom:1px solid #cecece;
    font-weight:100;
    font-size:1.1em;
    color:#333333;
    letter-spacing:0.3px;
}

.day-content p {
    white-space:normal;
    max-width:90%;
    line-height:1.1;
    word-wrap:break-word;
}

input[type=checkbox] {
    margin-right: 10px;
}

.trash-icon {
    font-size: 0.8em;
}

.trash-icon:hover,
.trash-icon:focus {
  box-shadow: 0 0.5em 0.5em -0.4em black;
  -webkit-animation:spin 1.5s linear alternate;
          animation:spin 1.5s linear alternate;
}

@-webkit-keyframes spin {
    100% {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@keyframes spin {
    100% {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@media (max-width: 600px) {
    .day-content {
        font-size: 1em;
    }
}
.buttons {
  width:100%;
  height:10%;
  display:flex;
  justify-content:center;
  align-items:center;
}

.btn{
  width:10%;
  height:60%;
  border:1px solid #efefef;
  float:left;
  text-align:center;
  font-family:inherit;
  cursor:pointer;
  position:relative;
  box-sizing:border-box;
  overflow:hidden;
  display:flex;
  justify-content:center;
  align-items:center;
  margin:10px;
  margin-bottom: 15px;
  background-color:#fff;
}

.btn span{
  font-size:1.1em;
  text-decoration:none;
  transition:all .5s ease;
  z-index:2;
  position:relative;
}

.eff{
  width:100%;
  height:100%;
  bottom:-50px;
  background:#000000;
  position:absolute;
  transition:all .5s ease;
  z-index:1;
}

.btn:hover .eff{
  bottom:0;
}

.btn:hover span{
  color:#fff;
}

.btn:focus {
  outline:none;
}

@media (max-width: 800px) {
  .btn {
    padding:10px;
  }
}

@media (max-width: 600px) {
  .buttons {
    flex-direction: column;
  }
  .btn {
    width:35%;
  }
}
